import BreadCrumb from "components/Breadcrumb";
import React from "react";
import { Link, useNavigate } from "react-router-dom";

import useSiteSearch from "./hooks";

export default function HomeSearch() {
  const { inputValue, searchResults, handleInputValueChange } = useSiteSearch();
  const navigate = useNavigate();

  return (
    <div className="">
      <div className="inline-flex flex-col justify-center relative text-gray-500">
        <div className="relative">
          <input
            type="text"
            className="p-2 pl-8 rounded border border-gray-200 bg-gray-200 focus:bg-white focus:outline-none focus:ring-2 focus:ring-yellow-600 focus:border-transparent"
            placeholder="search..."
            value={inputValue}
            onChange={handleInputValueChange}
          />
          <svg
            className="w-4 h-4 absolute left-2.5 top-3.5"
            xmlns="http://www.w3.org/2000/svg"
            fill="none"
            viewBox="0 0 24 24"
            stroke="currentColor"
          >
            <path
              strokeLinecap="round"
              strokeLinejoin="round"
              strokeWidth="2"
              d="M21 21l-6-6m2-5a7 7 0 11-14 0 7 7 0 0114 0z"
            />
          </svg>
        </div>
        <div className="bg-white border border-gray-100 w-full mt-2">
          {searchResults &&
            searchResults.map(({ item }) => {
              return (
                <div
                  key={item.title}
                  className="p-5 border border-1 border-neutral-200 hover:cursor-pointer"
                  onClick={() => navigate(item.path)}
                >
                  <Link className="" to={item.path}>
                    {item.title}
                  </Link>
                  <BreadCrumb
                    path={item.path}
                    crumbSeparatorClassName="!w-4 !h-4"
                    crumbClassName="!text-xs !text-gray-400"
                    wrapperClassName="!px-2"
                    hideHome
                  />
                </div>
              );
            })}
        </div>
      </div>
    </div>
  );
}
